<template>
  <div class="school-video-detail" id="school-video-detail" v-if="lesson">
    <div class="video" @click="playVideo">
      <img v-if="!showVideo" :src="lesson.cover_url" alt="">
      <i v-if="!showVideo" class="ui video play outline icon"></i>
      <video v-if="showVideo" id="video" :src="lesson.video_url" controls="controls" controlslist="nodownload" preload="metadata">你的浏览器不支持此视频播放。</video>
    </div>
    <div class="video-title">
      <i class="ui bookmark icon"></i>
      <p>{{lesson.title}}</p>
    </div>

    <div class="video-info">
      <p>
        <span class="info-label"><i class="ui tag icon"></i>年级</span>
        {{gradeLabel}}
      </p>
      <p>
        <span class="info-label"><i class="ui star icon"></i>学科</span>
        {{subjectLabel}}
      </p>
      <p>
        <span class="info-label"><i class="ui clock outline icon"></i>时长</span>{{lesson.duration}}
      </p>
    </div>
  </div>
</template>

<script>
import { GRADES, SUBJECTS } from 'assets/js/consts/common'

export default {
  name: 'school-video-detail',
  data() {
    return {
      lesson: null,
      showVideo: false,
    }
  },
  mounted() {
    this.lesson = this.$route.query
  },
  computed: {
    gradeLabel() {
      const { grade_id: grade } = this.lesson
      return GRADES[grade] || grade
    },
    subjectLabel() {
      const { subject_id: subject } = this.lesson
      return SUBJECTS[subject] || subject
    },
  },
  methods: {
    playVideo() {
      this.showVideo = true
      this.$nextTick(() => {
        document.getElementById('video').play()
      })
    },

  },
}
</script>

<style lang="less">
body {
  background: #f7f7f7;
}
</style>

<style lang="less" scoped>
@import '~assets/css/base-variables.less';
@block-base-padding: 0.2rem;

.school-video-detail {
  font-size: 0.4rem;
  margin-top: 1.2rem;
}
.video-title {
  padding: 0.3em @block-base-padding;
  background: white;
  margin-top: -0.1rem;
  color: @color-extra-light-black;
  p {
    margin: 0 0 0 1.2em;
    font-size: 0.5rem;
    line-height: 1.4;
    text-align: left;
  }
  .ui.icon {
    float: left;
    line-height: 1.4;
    color: @color-red;
    margin: 0.2em 0.3em 0 -0.2em;
  }
}
.video-info {
  background: white;
  padding: @block-base-padding;
  margin-top: 0.4rem;
  p {
    color: #888;
    font-size: 0.35rem;
  }
  .info-label {
    display: inline-block;
    width: 2.5rem;
    color: @color-light-black;
    .ui.icon {
      color: @color-blue;
    }
  }
}

.video {
  position: relative;
  img,
  video {
    width: 100%;
  }
  .ui.play.icon {
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
    left: 50%;
    color: rgba(255, 255, 255, 0.7);
    font-size: 1.5rem;
    text-shadow: 2px 2px rgba(0, 0, 0, 0.2);
  }
}
</style>



