<template>
  <div class="top-school-videos" id="top-school-videos" v-if="school">
    <div class="banner">
      <img :src="school.posterUrl" alt="">
    </div>
    <div class="action-bar">
      <div class="ui dropdown scrolling select-school">
        <div class="default text"></div>
        <i class="dropdown icon"></i>
        <div class="menu">
          <div class="item" v-for="s of schools" :data-value="s.id" :key="s.id" @click="changeSchool(s)">{{displaySchoolName(s.name)}}</div>
        </div>
      </div>
      <div class="ui dropdown scrolling select-subject">
        <div class="default text"></div>
        <i class="dropdown icon"></i>
        <div class="menu">
          <div class="item" v-for="subjectId of subjectOrder" :data-value="subjectId" :key="subjectId" @click="changeSubject(subjectId)">{{subjects[subjectId]}}</div>
        </div>
      </div>
    </div>
    <div class="lesson-list" >
      <div class="lesson-card" v-for="lesson of lessons" :key="lesson.id" @click="$router.push({ name: 'school-video-detail', params: { lessonId: lesson.id }})">
        <div class="lesson-cover">
          <div class="lesson-label label-subject">{{subjects[lesson.subject_id]}}</div>
          <div class="lesson-label label-grade" v-if="lesson.isSenior">高中</div>
          <div class="lesson-label label-grade" v-if="lesson.isJunior">初中</div>
          <img :src="lesson.cover_url">
        </div>
        <div class="lesson-title">
          <p>{{lesson.name}}</p>
        </div>
      </div>
    </div>
    <scroll scroll-container="top-school-videos" :handler="scrollFetchLesson" :should-handle="!loading && !stopLoading" class="mugen-loading">
      Loading...
    </scroll>
  </div>
</template>

<script>
import { SUBJECTS, SUBJECT_BASE_ORDER } from 'assets/js/consts/common'
import axios from 'axios'

var Scroll = require('static/vue-mugen-scroll')

export default {
  name: 'top-school-index',
  data() {
    return {
      schools: [],
      school: null,
      subjects: SUBJECTS,
      subjectOrder: SUBJECT_BASE_ORDER,
      loading: false,
      lessons: [],
      subjectId: 0,
      stopLoading: false,
    }
  },
  components: {
    scroll: Scroll.MugenScroll,
  },
  mounted() {
    axios.get('/pad/j/campaign/top-school-videos', { params: { action: 'fetchSchool', subjectId: 0 } })
      .then((response) => {
        const data = response.data
        this.schools = data.schools
        this.school = data.schools[0]
        this.lessons = data.lessons
        this.$nextTick(() => {
          $('.ui.dropdown.select-school').dropdown('set selected', this.school.id)
          $('.ui.dropdown.select-subject').dropdown('set selected', '0')
        })
      })
  },
  methods: {
    displaySchoolName(schoolName) {
      return schoolName === '龙口一中东校区' ? '龙口一中' : schoolName
    },
    changeSchool(school) {
      this.school = school
      this.fetchAndReplaceLesson()
      this.stopLoading = false
    },
    changeSubject(subjectId) {
      this.subjectId = subjectId
      this.fetchAndReplaceLesson()
      this.stopLoading = false
    },
    fetchLesson(start = 0) {
      const params = {
        action: 'fetchLesson',
        subjectId: this.subjectId,
        schoolId: this.school.id,
        start,
        limit: 20,
      }
      return axios.get('/pad/j/campaign/top-school-videos', { params })
    },
    scrollFetchLesson() {
      this.loading = true
      $('.mugen-loading').text('Loading')
      this.fetchLesson(this.lessons.length).then((response) => {
        if (response.data.lessons.length) {
          this.lessons = this.lessons.concat(response.data.lessons)
        } else {
          this.stopLoading = true
          $('.mugen-loading').text('')
        }
        this.loading = false
      })
    },
    fetchAndReplaceLesson() {
      this.fetchLesson().then((response) => {
        this.lessons = response.data.lessons
      })
    },

  },
}
</script>

<style lang="less" scoped>
  @import '~assets/css/base-variables.less';

  @lesson-border-radius: .3em;

  .lesson-card {
    cursor: pointer;
    margin: .4em .6em;
    display: inline-block;
    width: 45%;
    border-radius: @lesson-border-radius;
    box-shadow: 1px 2px 2px 0px rgba(181, 181, 181, 0.33), -1px 0 2px 0px rgba(181, 181, 181, 0.33);
    .lesson-cover {
      img {
        width: 4.5rem;
        height: 2.8rem;
      }
      position: relative;
      border-radius: @lesson-border-radius @lesson-border-radius 0 0;
      margin-bottom: -.1rem;
    }
    .lesson-label {
      box-shadow: 1px 2px 2px 0px rgba(181, 181, 181, 0.33);
      position: absolute;
      top: .6em;
      color: white;
      font-size: .25rem;
      padding: .2em .4em;
      margin: 0;
      display: inline-block;
    }
    .label-subject {
      background: #FFA200;
      padding: .2em .4em .2em 3.2em;
    }
    .label-grade {
      background: #FF5000;
    }
    .lesson-title {
      width: 100%;
      word-break: break-all;
      height: 1rem;
      overflow: hidden;
      background: white;
      padding: .1rem .1rem 0 .1rem;
      box-sizing: border-box;
      border-radius: 0 0 @lesson-border-radius @lesson-border-radius;
      color: @color-extra-light-black;
      p {
        font-size: .34rem;
        line-height: .45rem;
        margin: 0;
        letter-spacing: 0;
        letter-spacing: 0;
      }
    }
  }
  .top-school-videos {
    width: 100%;
    height: 100%;
    min-height: 100vh;
    background-color: #F4F4F4;
    font-size: .4rem;
  }
  .banner {
    width: 100%;
    background: white;
  }
  .banner img {
    width: 100%;
  }
  .ui.dropdown {
    font-size: .4rem;
    color: #FF9100;
  }
  .ui.dropdown>.dropdown.icon {
    margin: 0 0 0 .5em;
  }
  .ui.dropdown .menu {
    border-radius: .3em;
  }
  .ui.dropdown .menu>.item {
    font-size: .9em;
    padding: .8em 1.5em !important;
  }
  .action-bar {
    background: white;
    clear: both;
    box-shadow: 0px 2px 2px 0px rgba(181, 181, 181, 0.33);
    padding: .5em;
    margin-bottom: .5em;
    &:after {
      content: "";
      visibility: hidden;
      display: block;
      height: 0;
      clear: both;
    }
  }


  .ui.dropdown.select-school {
    float: left;
  }
  .ui.dropdown.select-subject {
    float: right;
  }
  .mugen-loading {
    text-align: center;
    color: #888;
    margin-top: .3rem;
  }


</style>



