<template>
  <div class="header" v-if="isDjt">
    <div class="left" @click="handleBackClick">
      <img src="./../../assets/img/back.png" />
      返回
    </div>
    沃未来名师视频
    <div class="right"></div>
  </div>
</template>

<script>
export default {
  name: 'v-header',
  computed: {
    isDjt() {
      const { pathname } = location
      return pathname.match(/\/djt\/detail(\/)?$/) || pathname.match(/\/djt(\/)?$/)
    },
  },
  methods: {
    handleBackClick() {
      const { pathname } = location
      if (pathname.match(/\/djt\/detail(\/)?$/)) return history.back()
      if (pathname.match(/\/djt(\/)?$/)) {
        window.dd.biz.navigation.close({
          onSuccess() { },
          onFail() { },
        })
      }
    },
  },
}
</script>

<style lang="less" scoped>
@import '~assets/css/base-variables.less';

.header {
  height: 1.2rem;
  background: #ffa200;
  width: 100%;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-shrink: 0;
  padding: 0.2rem 0.1rem;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 99;
  font-size: 0.5rem;
  box-shadow: 0 0px 20px #666;
  .left,
  .right {
    display: inline-block;
    display: flex;
    align-items: center;
    width: 20%;
  }
  img {
    height: 0.55rem;
    width: 0.55rem;
  }
}
</style>




