import Vue from 'vue'
import Raven from 'raven-js'
import RavenVue from 'raven-js/plugins/vue'
import VueRouter from 'vue-router'

import TopVideos from './top-school-video'
import VideoDetail from './school-video-detail'
import DDVideos from './videos'
import DDVideoDetail from './video-detail'
import VHeader from './header'

require('./index.less')

window.Promise = require('bluebird')

Vue.config.productionTip = false

const axios = require('axios')

axios.defaults.headers.common.X_REQUESTED_WITH = 'XMLHttpRequest'
axios.defaults.xsrfHeaderName = 'X-CSRFTOKEN'
axios.defaults.xsrfCookieName = 'csrftoken'
Vue.prototype.$raven = Raven
if (process.env.NODE_ENV === 'production') {
  Raven.config('http://216e3d581cd9424fad7f5d8c046b2f64@sentry.xdf100.com/6').addPlugin(RavenVue, Vue).install()
}

Vue.use(VueRouter)


const router = new VueRouter({
  mode: 'history',
  base: process.env.NODE_ENV === 'production' ? '/pad/campaign' : '/static/w/pages/campaign.html',
  routes: [
    {
      path: '', component: TopVideos, name: 'top-videos', alias: '/top-school-videos', meta: { title: '名师视频' },
    },
    {
      path: '/top-school-videos/:lessonId', component: VideoDetail, name: 'school-video-detail', meta: { title: '名师视频' },
    },
    {
      path: '/djt', component: DDVideos, meta: { title: '钉教通视频' },
    },
    {
      path: '/djt/detail', component: DDVideoDetail, meta: { title: '视频详情' },
    },
  ],
})

router.beforeEach((to, from, next) => {
  document.title = to.meta.title
  next()
});

(async () => {
  process.env.NODE_ENV === 'development' && await import(/* webpackChunkName: "mock" */ '@/mock')
  new Vue({
    delimiters: ['${', '}'],
    el: '#app',
    router,
    data: {},
    components: { VHeader },
  })
})()



