<template>
  <div class="school-video-detail" id="school-video-detail" v-if="lesson">
    <div class="video" @click="playVideo">
      <img v-if="!showVideo" :src="lesson.cover_url" alt="">
      <i v-if="!showVideo" class="ui video play outline icon"></i>
      <video v-if="showVideo" id="video" :src="lesson.video_url" controls="controls" controlslist="nodownload" preload="metadata">你的浏览器不支持此视频播放。</video>
    </div>
    <div class="video-title">
      <i class="ui bookmark icon"></i>
      <p>{{lesson.name}}</p>
    </div>

    <div class="video-info">
      <p>
        <span class="info-label"><i class="ui university icon"></i>学校</span>{{displaySchoolName(lesson.school_name)}}
      </p>
      <p>
        <span class="info-label"><i class="ui tag icon"></i>学段/学科</span>
        {{gradeLabel}} {{subjectLabel}}
      </p>
    </div>
  </div>
</template>

<script>
import { SUBJECTS, JUNIOR_HIGH_SCHOOL_GRADES } from 'assets/js/consts/common'
import axios from 'axios'

export default {
  name: 'school-video-detail',
  data() {
    return {
      lesson: null,
      showVideo: false,
    }
  },
  created() {
    axios.get('/pad/j/manage/camp/lesson', { params: { lesson_id: this.$route.params.lessonId } }).then((response) => {
      this.lesson = response.data.lesson
    })
  },
  mounted() {
  },
  computed: {
    gradeLabel() {
      if (!this.lesson) {
        return ''
      }
      if (this.lesson.grade_id in JUNIOR_HIGH_SCHOOL_GRADES) {
        return '初中'
      }
      return '高中'
    },
    subjectLabel() {
      if (!this.lesson) {
        return ''
      }
      return SUBJECTS[this.lesson.subject_id]
    },
  },
  methods: {
    displaySchoolName(schoolName) {
      return schoolName === '龙口一中东校区' ? '龙口一中' : schoolName
    },
    playVideo() {
      this.showVideo = true
      this.$nextTick(() => {
        document.getElementById('video').play()
      })
    },

  },
}
</script>

<style lang="less">
body {
  background: #f7f7f7;
}
</style>

<style lang="less" scoped>
@import '~assets/css/base-variables.less';
@block-base-padding: .2rem;

.school-video-detail {
  font-size: .4rem;
}
.video-title {
  padding: .3em @block-base-padding;
  background: white;
  margin-top: -.1rem;
  color: @color-extra-light-black;
  p {
    margin: 0 0 0 1.2em;
    font-size: .5rem;
    line-height: 1.4;
    text-align: left;
  }
  .ui.icon {
    float: left;
    line-height: 1.4;
    color: @color-red;
    margin: .2em .3em 0 -.2em;
  }
}
.video-info {
  background: white;
  padding: @block-base-padding;
  margin-top: .4rem;
  p {
    color: #888;
    font-size: .35rem;
  }
  .info-label {
    display: inline-block;
    width: 2.5rem;
    color: @color-light-black;
    .ui.icon {
      color: @color-blue;
    }
  }
}

.video {
  position: relative;
  img, video {
    width: 100%
  }
  .ui.play.icon {
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
    left: 50%;
    color: rgba(255, 255, 255, 0.7);
    font-size: 1.5rem;
    text-shadow: 2px 2px rgba(0, 0, 0, 0.2);
  }
}
</style>



