<template>
  <div class="top-school-videos" id="top-school-videos">
    <div class="banner">
      <img src="~assets/img/dingxiaotong.png" alt="">
    </div>
    <div class="action-bar">
      <div class="ui dropdown scrolling select-school">
        <div class="default text"></div>
        <i class="dropdown icon"></i>
        <div class="menu">
          <div class="item" v-for="[key,value] in grades" :data-value="key" :key="key" @click="changeSchool(key)">{{value}}</div>
        </div>
      </div>
      <div class="ui dropdown scrolling select-subject">
        <div class="default text"></div>
        <i class="dropdown icon"></i>
        <div class="menu">
          <div class="item" v-for="subjectId of subjectOrder" :data-value="subjectId" :key="subjectId" @click="changeSubject(subjectId)">{{subjects[subjectId]}}</div>
        </div>
      </div>
    </div>
    <div class="lesson-list" v-if="permission">
      <div class="lesson-card" v-for="lesson of lessons" :key="lesson.id" @click="$router.push({ path: '/djt/detail', query: lesson})">
        <div class="lesson-cover">
          <div class="lesson-label label-subject">{{subjectConvert(lesson.subject_id)}}</div>
          <div class="lesson-label label-grade">{{gradeConvert(lesson.grade_id)}}</div>
          <img :src="lesson.cover_url">
        </div>
        <div class="lesson-title">
          <p>{{lesson.title}}</p>
        </div>
      </div>
    </div>
    <div v-if="!permission" style="text-align: center">没有权限</div>
    <div v-if="!lessons.length" style="text-align: center">没有数据</div>
    <scroll scroll-container="top-school-videos" :handler="scrollFetchLesson" :should-handle="!loading && !stopLoading" class="mugen-loading">
      Loading...
    </scroll>
  </div>
</template>

<script>
import { SUBJECTS, SUBJECT_BASE_ORDER, GRADES } from 'assets/js/consts/common'
import axios from 'axios'

const genGrades = (data) => {
  const tmp = Object.entries(data)
    .filter(([key]) => [20, 10, 6, 2, 1].indexOf(parseInt(key)) === -1)
    .sort(([a], [b]) => parseInt(b) - parseInt(a))
  const allGradeColumn = tmp.splice(-1, 1)
  return [...allGradeColumn, ...tmp]
}

const Scroll = require('static/vue-mugen-scroll')

export default {
  name: 'top-school-index',
  data() {
    return {
      grades: genGrades(GRADES),
      grade: 0,
      subjects: SUBJECTS,
      subjectOrder: SUBJECT_BASE_ORDER,
      loading: false,
      lessons: [],
      subjectId: 0,
      stopLoading: false,
      permission: true,
      params: null,
    }
  },
  components: {
    scroll: Scroll.MugenScroll,
  },
  mounted() {
    const { query } = this.$route
    if (query.authCode === undefined) {
      this.permission = false
      $('.mugen-loading').text('')
    }
    this.$nextTick(() => {
      $('.ui.dropdown.select-school').dropdown('set selected', '0')
      $('.ui.dropdown.select-subject').dropdown('set selected', '0')
    })
  },
  methods: {
    changeSchool(grade) {
      this.grade = parseInt(grade)
      this.fetchAndReplaceLesson()
      this.stopLoading = false
    },
    changeSubject(subjectId) {
      this.subjectId = subjectId
      this.fetchAndReplaceLesson()
      this.stopLoading = false
    },
    fetchLesson(start = 0) {
      const params = {
        subject: this.subjectId,
        grade: this.grade,
        start,
        limit: 20,
        ...this.$route.query,
      }
      return axios.get('/dingding/xdf_videos/', { params })
    },
    scrollFetchLesson() {
      this.loading = true
      $('.mugen-loading').text('Loading')
      this.fetchLesson(this.lessons.length).then((response) => {
        if (!this.validate(response.data.status)) return
        if (response.data.videos.length) {
          this.lessons = this.lessons.concat(response.data.videos)
        } else {
          this.stopLoading = true
          $('.mugen-loading').text('')
        }
        this.loading = false
      })
    },
    fetchAndReplaceLesson() {
      this.fetchLesson().then((response) => {
        if (!this.validate(response.data.status)) return
        this.lessons = response.data.videos
        this.$nextTick(() => {
          if (!this.lessons.length) {
            $('.mugen-loading').text('')
          } else {
            $('.mugen-loading').text('Loading')
          }
        })
      })
    },
    gradeConvert(grade) {
      return GRADES[grade] || grade
    },
    subjectConvert(subject) {
      return SUBJECTS[subject] || subject
    },
    validate(status) {
      if (status || !this.permission) {
        $('.mugen-loading').text('')
        this.permission = false
        return false
      }
      return true
    },

  },
}
</script>

<style lang="less" scoped>
@import '~assets/css/base-variables.less';

@lesson-border-radius: 0.3em;

.top-school-videos {
  margin-top: 1.2rem;
}

.lesson-card {
  cursor: pointer;
  margin: 0.4em 0.6em;
  display: inline-block;
  width: 45%;
  border-radius: @lesson-border-radius;
  box-shadow: 1px 2px 2px 0px rgba(181, 181, 181, 0.33),
    -1px 0 2px 0px rgba(181, 181, 181, 0.33);
  .lesson-cover {
    img {
      width: 4.5rem;
      height: 2.8rem;
    }
    position: relative;
    border-radius: @lesson-border-radius @lesson-border-radius 0 0;
    margin-bottom: -0.1rem;
  }
  .lesson-label {
    box-shadow: 1px 2px 2px 0px rgba(181, 181, 181, 0.33);
    position: absolute;
    top: 0.6em;
    color: white;
    font-size: 0.25rem;
    padding: 0.2em 0.4em;
    margin: 0;
    display: inline-block;
  }
  .label-subject {
    background: #ffa200;
    padding: 0.2em 0.4em 0.2em 3.2em;
  }
  .label-grade {
    background: #ff5000;
  }
  .lesson-title {
    width: 100%;
    word-break: break-all;
    height: 1rem;
    overflow: hidden;
    background: white;
    padding: 0.1rem 0.1rem 0 0.1rem;
    box-sizing: border-box;
    border-radius: 0 0 @lesson-border-radius @lesson-border-radius;
    color: @color-extra-light-black;
    p {
      font-size: 0.34rem;
      line-height: 0.45rem;
      margin: 0;
      letter-spacing: 0;
      letter-spacing: 0;
    }
  }
}
.top-school-videos {
  width: 100%;
  height: 100%;
  min-height: 100vh;
  background-color: #f4f4f4;
  font-size: 0.4rem;
}
.banner {
  width: 100%;
  background: white;
}
.banner img {
  width: 100%;
}
.ui.dropdown {
  font-size: 0.4rem;
  color: #ff9100;
}
.ui.dropdown > .dropdown.icon {
  margin: 0 0 0 0.5em;
}
.ui.dropdown .menu {
  border-radius: 0.3em;
}
.ui.dropdown .menu > .item {
  font-size: 0.9em;
  padding: 0.8em 1.5em !important;
}
.action-bar {
  background: white;
  clear: both;
  box-shadow: 0px 2px 2px 0px rgba(181, 181, 181, 0.33);
  padding: 0.5em;
  margin-bottom: 0.5em;
  &:after {
    content: '';
    visibility: hidden;
    display: block;
    height: 0;
    clear: both;
  }
}

.ui.dropdown.select-school {
  float: left;
}
.ui.dropdown.select-subject {
  float: right;
}
.mugen-loading {
  text-align: center;
  color: #888;
  margin-top: 0.3rem;
}
</style>



